import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { DataService } from '../data.service';

@Component({
  selector: 'app-blocks',
  templateUrl: './blocks.component.html',
  styleUrls: ['./blocks.component.css']
})
export class BlocksComponent implements OnInit {

  constructor(private route: ActivatedRoute, private router: Router, private ds: DataService) { }
  yards;
  blocks;
  content=[];
  block_types=[];
  page;
  max_page: number;
  pagination_left = [];
  pagination_right = [];
  yard;
  quarry;
  quarries;
  fa;
  fp;
  fpo
  name;
  catagories;
  block_type;
  layer_type;
  gt;
  lt;
  l_gt;
  l_lt;
  h_gt;
  h_lt;
  w_gt;
  w_lt;
  l_date;
  g_date;
  log;
  drawer=0;
  drawer_items=[];
  units;
  unit;
  factor;

  no_of_items = 0
  total_slabs_area = 0
  total_block_weight = 0
  total_cost = 0
  total_slabs_area_after_minor = 0

 
  ngOnInit(): void {
    this.name = localStorage.getItem('name').slice(0, 8)
    document.getElementById("myDropdown").classList.toggle("show");
    this.ds.allBlocks({ token: localStorage.getItem('token') }).subscribe((res) => {
      if (res.status == 'ok') {
        this.blocks = res.data;
      }
    })
    
    this.ds.getYards({ token: localStorage.getItem('token') }).subscribe((res) => {
      if (res.status == 'ok') {
        this.yards = res.data;
      }
    });
    this.ds.getUnits({token: localStorage.getItem('token')}).subscribe((res)=>{
      if (res.status=="ok") {
        this.units = res.data;
      }
    });
    this.ds.getQuarries({token:localStorage.getItem('token')}).subscribe((res)=>{
      if (res.status=="ok") {
        this.quarries = res.data
      }
    })
    this.route.queryParams.subscribe((par) => {
        this.yard = par.yard;
        this.page = parseInt(par.page)
        this.fa = (par.fa);
        this.fp = (par.fp);
        this.block_type = par.block_type;
        this.layer_type = par.layer_type;
        this.lt = par.lt;
        this.gt = par.gt;
        this.l_gt = par.l_gt;
        this.l_lt = par.l_lt;
        this.h_gt = par.h_gt;
        this.h_lt = par.h_lt;
        this.w_gt = par.w_gt;
        this.w_lt = par.w_lt;
        this.fpo = par.fpo;
        this.unit = localStorage.getItem('unit')
        this.factor = localStorage.getItem('factor')
        this.l_date = par.l_date
        this.g_date = par.g_date

        this.ds.getItems({
          block_no:par.block,
          token: localStorage.getItem('token'),
          yard: this.yard,
          page: this.page,
          fa: this.fa,
          fp: this.fp,
          l_gt : this.l_gt,
          l_lt : this.l_lt,
          h_gt : this.h_gt,
          h_lt : this.h_lt,
          w_gt : this.w_gt,
          w_lt : this.w_lt,
          unit: this.unit,
          factor: this.factor,
          fpo: this.fpo,
          block_type: this.block_type,
          layer_type: this.layer_type,
          lt: this.lt,
          gt: this.gt,
          l_date: this.l_date,
          g_date: this.g_date

        }).subscribe((res) => {
          if (res.status == 'ok') {
            document.getElementById('ax').style.background = '';
            document.getElementById('tx').style.background = '';
            document.getElementById('fx').style.background = '';
            document.getElementById('az').style.background = '';
            document.getElementById('tz').style.background = '';
            document.getElementById('fz').style.background = '';
            document.getElementById('ay').style.background = '';
            document.getElementById('ty').style.background = '';
            document.getElementById('fy').style.background = '';
            if (this.fa == 'a' || this.fa == undefined) {
              document.getElementById('ax').style.background = '#bbd2dd';
            }
            if (this.fa == 't') {
              document.getElementById('tx').style.background = '#bbd2dd';
            }
            if (this.fa == 'f') {
              document.getElementById('fx').style.background = '#bbd2dd';
            }

            if (this.fp == 'a' || this.fp == undefined) {
              document.getElementById('az').style.background = '#bbd2dd';
            }
            if (this.fp == 't') {
              document.getElementById('tz').style.background = '#bbd2dd';
            }
            if (this.fp == 'f') {
              document.getElementById('fz').style.background = '#bbd2dd';
            }
            if (this.fpo == 'a' || this.fpo == undefined) {
              document.getElementById('ay').style.background = '#bbd2dd';
            }
            if (this.fpo == 't') {
              document.getElementById('ty').style.background = '#bbd2dd';
            }
            if (this.fpo == 'f') {
              document.getElementById('fy').style.background = '#bbd2dd';
            }
            this.content = res.data;
            this.no_of_items = res.no_of_items;
            this.total_block_weight = res.total_block_weight
            this.total_slabs_area = res.total_slabs_area
            this.total_cost = res.total_cost
            this.total_slabs_area_after_minor = res.total_slabs_area_after_minor
            let i=0;
            this.change_unit()
            this.max_page = res.max_page;
            this.page = res.page
            this.pagination_right = [];
            this.pagination_left = [];
            if (this.page - 1 > 0) {
              this.pagination_left.push(this.page - 1);
              if (this.page - 2 > 0) {
                this.pagination_left.push('...')
                this.pagination_left.push(1);
              }
            }
            this.pagination_left = this.pagination_left.reverse()
            if ((this.page + 1) < (this.max_page + 1)) {
              this.pagination_right.push(this.page + 1);
              if (this.page + 2 < this.max_page + 1) {
                this.pagination_right.push('...')
                this.pagination_right.push(this.max_page);
              }
            }
            this.ds.drawerItems({token:localStorage.getItem('token')}).subscribe((res)=>{
              if (res.status=="ok"){
                this.drawer_items = res.data;
                this.drawer = res.data.length
                for (let i=0; i<this.content.length;i++) {
                  for (let j=0; j<this.drawer_items.length; j++) {
                    if (this.content[i].block_no == this.drawer_items[j].block_no) {
                      this.content[i].drawer = true
                    }
                  }
                }
              }
            })
          }
        })
    })
  }

  action(i) {
    if (this.content[i].slabs) {
      this.router.navigate(['/main/slabs'],
      { queryParams: { block: this.content[i].block_no, unit: this.unit} });
      return
    }
    this.router.navigate(['/main/block'],
      { queryParams: { block: this.content[i].block_no , unit: this.unit} });
  }
  actionp(w) {
    if (w != '...') {
      this.router.navigate(['/main/blocks'],
        {
          queryParams: {
            yard: this.yard,
            page: w,
            fp: this.fp,
            fa: this.fa,
            block_type: this.block_type,
            layer_type: this.layer_type,
            lt: this.lt,
            gt: this.gt,
            unit: this.unit,

          l_gt : this.l_gt,
          l_lt : this.l_lt,
          h_gt : this.h_gt,
          h_lt : this.h_lt,
          w_gt : this.w_gt,
          w_lt : this.w_lt,
          factor: this.factor,
          l_date: this.l_date,
          g_date: this.g_date,
          fpo: this.fpo,
          }
        })

    }
  }

  pFilter(a) {
    this.fp = a;
    document.getElementById("az").style.background = "";
    document.getElementById("tz").style.background = "";
    document.getElementById("fz").style.background = "";
    document.getElementById(a + "z").style.background = "#bbd2dd";
    
    document.getElementById("ay").style.background = "#bbd2dd"
    document.getElementById("ty").style.background = ""
    document.getElementById("fy").style.background = ""
    this.fpo='a'
    this.router.navigate(['/main/blocks'],
      {
        queryParams: {
          yard: this.yard,
          fp: this.fp,
          fa: this.fa,
          fpo:this.fpo,
          lt: this.lt,
          gt: this.gt,
          block_type: this.block_type,
          layer_type: this.layer_type,
          unit: this.unit,
          l_gt : this.l_gt,
          l_lt : this.l_lt,
          h_gt : this.h_gt,
          h_lt : this.h_lt,
          w_gt : this.w_gt,
          w_lt : this.w_lt,
          factor: this.factor,
          l_date: this.l_date,
          g_date: this.g_date,
        }
      })
  }

  aFilter(a) {
    this.fa = a;
    document.getElementById("ax").style.background = "";
    document.getElementById("tx").style.background = "";
    document.getElementById("fx").style.background = "";
    document.getElementById(a + "x").style.background = "#bbd2dd";

    this.router.navigate(['/main/blocks'],
      {
        queryParams: {
          yard: this.yard,
          fp: this.fp,
          fa: this.fa,
          fpo: this.fpo,
          lt: this.lt,
          gt: this.gt,
          block_type: this.block_type,
          layer_type: this.layer_type,
          unit: this.unit,

          l_gt : this.l_gt,
          l_lt : this.l_lt,
          h_gt : this.h_gt,
          h_lt : this.h_lt,
          w_gt : this.w_gt,
          w_lt : this.w_lt,
          factor: this.factor,
          l_date: this.l_date,
          g_date: this.g_date,
        }
      })
  }

  poFilter(a) {
    this.fpo = a;
    document.getElementById("ay").style.background = "";
    document.getElementById("ty").style.background = "";
    document.getElementById("fy").style.background = "";
    document.getElementById(a + "y").style.background = "#bbd2dd";
    this.router.navigate(['/main/blocks'],
      {
        queryParams: {
          yard: this.yard,
          fp: this.fp,
          fa: this.fa,
          fpo: this.fpo,
          lt: this.lt,
          gt: this.gt,
          block_type: this.block_type,
          layer_type: this.layer_type,
          unit: this.unit,
          l_gt : this.l_gt,
          l_lt : this.l_lt,
          h_gt : this.h_gt,
          h_lt : this.h_lt,
          w_gt : this.w_gt,
          w_lt : this.w_lt,
          factor: this.factor,
          l_date: this.l_date,
          g_date: this.g_date,
        }
      })
  }

  search() {
    var input, filter, ul, li, a, i;
    input = document.getElementById("myInput");
    filter = input.value.toLowerCase();
    var div = document.getElementById("myDropdown");
    a = div.getElementsByTagName("a");
    var txtValue;

    for (i = 0; i < a.length; i++) {

      txtValue = (a[i].innerText.toLowerCase()).split('zzz000')[0]+' - '+(a[i].innerText.toLowerCase()).split('zzz000')[1];

      if (txtValue.indexOf(filter) > -1) {
        a[i].style.display = "block";
      } else {
        a[i].style.display = "none";
      }
    }
    if (filter == '') {
      document.getElementById('cont').style.display = 'none'

    }
    else {
      document.getElementById('cont').style.display = 'block'
    }
  }

  populate_search(i) {
    this.router.navigate(['/main/blocks'],
      { queryParams: { block: this.blocks[i].block_no, unit: this.unit , factor:this.factor} });
    
    document.getElementsByTagName('input')[0].value = '';
    document.getElementById('cont').style.display = 'none'

  }


  filter() {
    this.router.navigate(['/main/blocks'],
      {
        queryParams: {
          yard: this.yard,
          block_type: this.block_type,
          layer_type: this.layer_type,
          gt: this.gt,
          lt: this.lt,
          l_gt : this.l_gt,
          l_lt : this.l_lt,
          h_gt : this.h_gt,
          h_lt : this.h_lt,
          w_gt : this.w_gt,
          w_lt : this.w_lt,
          unit: this.unit,
          factor: this.factor,
          l_date: this.l_date,
          g_date: this.g_date,
          fa: this.fa,
          fp: this.fp,
          fpo: this.fpo,

        }
      });
  }

  toggle_item(i){
    this.ds.itemToggle({token:localStorage.getItem('token'), block_no:this.content[i].block_no}).subscribe((res)=>{
      if (res.status=="ok"){
        if (this.content[i].drawer == true) {
          this.content[i].drawer = false
          this.drawer-=1
        }
        else {
          this.content[i].drawer = true;
          this.drawer+=1
        }
      }
    })
  }

  clear_drawer(){
    if (!confirm("Clear drawer?")) return
    this.ds.clearDrawer({token:localStorage.getItem('token')})
      .subscribe((res)=>{
        if (res.status=="ok") {
          for (let i of this.content) {
            i.drawer = false
          }
          this.drawer = 0
        }
      })
  }

  change_unit() {
    for (let i=0; i<this.units.length; i++) {
      if (this.units[i].unit == this.unit) {
        for (let j of this.content) {
          j.unit = this.unit
          j.factor = this.units[i].factor
        }
        
        this.factor = this.units[i].factor
        localStorage.setItem('unit', this.unit)
        localStorage.setItem('factor', this.factor)
        break
      }
    }
  }

  block_report() {
    let filters = {
      yard: this.yard,
      fp: this.fp,
      fa: this.fa,
      block_type: this.block_type,
      layer_type: this.layer_type,
      lt: this.lt,
      gt: this.gt,
      unit: this.unit,
      factor: this.factor,
      l_gt : this.l_gt,
      l_lt : this.l_lt,
      h_gt : this.h_gt,
      h_lt : this.h_lt,
      w_gt : this.w_gt,
      w_lt : this.w_lt,
      l_date: this.l_date,
      g_date: this.g_date,
    }
    let query = this.serialize(filters, '')
    window.location.href=this.ds.report_url+"/block-report?token="+localStorage.getItem('token')
    +"&"+query
  }

  slabs_report() {
    let filters = {
      yard: this.yard,
      fa: this.fa,
      block_type: this.block_type,
      layer_type: this.layer_type,
      lt: this.lt,
      gt: this.gt,
      unit: this.unit,
      factor: this.factor,
      l_gt : this.l_gt,
      l_lt : this.l_lt,
      h_gt : this.h_gt,
      h_lt : this.h_lt,
      w_gt : this.w_gt,
      w_lt : this.w_lt,
      l_date: this.l_date,
      g_date: this.g_date,
    }
    let query = this.serialize(filters, '')
    window.location.href=this.ds.report_url+"/slabs-report?token="+localStorage.getItem('token')
    +"&"+query
  }

  overview_report() {
    let filters = {
      yard: this.yard,
      fa: this.fa,
      block_type: this.block_type,
      lt: this.lt,
      gt: this.gt,
      unit: this.unit,
      factor: this.factor,
      l_gt : this.l_gt,
      l_lt : this.l_lt,
      h_gt : this.h_gt,
      h_lt : this.h_lt,
      w_gt : this.w_gt,
      w_lt : this.w_lt,
      l_date: this.l_date,
      g_date: this.g_date,
    }
    let query = this.serialize(filters, '')
    window.location.href=this.ds.report_url+"/overview-report?token="+localStorage.getItem('token')
    +"&"+query
  }

  serialize(obj, prefix) {
    var str = [],
      p;
    for (p in obj) {
      if (obj.hasOwnProperty(p)) {
        var k = prefix ? prefix + "[" + p + "]" : p,
          v = obj[p];
        str.push((v !== null && typeof v === "object") ?
          this.serialize(v, k) :
          encodeURIComponent(k) + "=" + encodeURIComponent(v));
      }
    }
    return str.join("&");
  }

}
