import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
// import { setTimeout } from 'timers';
import { DataService } from '../data.service';

@Component({
  selector: 'app-block',
  templateUrl: './block.component.html',
  styleUrls: ['./block.component.css']
})
export class BlockComponent implements OnInit {

  constructor(private router: Router, private ds: DataService, private route: ActivatedRoute) { }
  name;
  b;
  quarry;
  yard;
  company;
  salesman;
  moderator;
  factor=1;
  units;
  
  ngOnInit(): void {
    this.name=localStorage.getItem('name').slice(0,8);
    this.route.queryParams.subscribe( (par)=>{
      this.ds.getBlock({token: localStorage.getItem('token'), block_no:par.block})
        .subscribe((res)=>{
          if (res.status == 'ok') {
            this.b = res.block;
            this.b.unit = res.unit.unit;
            this.b.factor = res.unit.factor?res.unit.factor:1;
            for (let i in this.b) {
              if (this.b[i]==undefined) {
                this.b[i]=0
              }
            }
            this.quarry = res.quarry;
            this.yard = res.yard;
            
          }
        });
      this.ds.getUnits({token: localStorage.getItem('token')}).subscribe((res)=>{
        if (res.status == "ok") {
          this.units = res.data
        }
      })
    })

    

    this.ds.getMyRoles({token:localStorage.getItem('token')}).subscribe((res)=>{
      if (res.status =='ok') {
        this.salesman = res.data.salesman_role;
        this.moderator = res.data.moderator_role;
      }
    })
  }

  edit() {
    if (this.b.is_child) {
      this.router.navigate(['/main/edit-thappi'],
     {queryParams:{block:this.b.block_no}});
     return
    }
    this.router.navigate(['/main/edit-block'],
     {queryParams:{block:this.b.block_no}});
  }

  delete() {
    if (!confirm('Are you sure you wanna delete block? All trades will be also deleted.')) return
    this.ds.deleteBlock({token:localStorage.getItem('token'), block_no: this.b.block_no})
      .subscribe((res)=>{
        if (res.status == "ok") {
          this.router.navigate(['/main/blocks'],
          {queryParams:{}});
        }
      })
  }
  un_break () {
    if (!confirm('Are you sure you wanna un-break? All trades of thappis will be deleted.')) return
    this.ds.unBreak({token: localStorage.getItem('token'), block_no:this.b.block_no})
      .subscribe((res)=>{
        if (res.status == "ok") {
          this.b.children=[]
          this.b.has_children=false
        }
      })
  }
  un_process() {
    if (!confirm('Are you sure you want to un-process this? All trades of slabs will be deleted.')) return
    this.ds.unProcess({token: localStorage.getItem('token'), block_no: this.b.block_no})
      .subscribe((res)=>{
        if (res.status == "ok") {
          this.b.slabs=false
        }
      })
  }

  change_unit() {
    for (let i=0; i<this.units.length; i++) {
      if (this.units[i].unit == this.b.unit) {
        this.b.factor = this.units[i].factor
        break
      }
    }
  }

}
