import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { DashboardComponent } from './dashboard/dashboard.component';
import { SigninComponent } from './signin/signin.component';
import { SignupComponent } from './signup/signup.component';
import { ChangepasswordComponent } from './changepassword/changepassword.component';
import { MainComponent } from './main/main.component';
import { ManageRolesComponent } from './manage-roles/manage-roles.component';
import { NewBlockComponent } from './new-block/new-block.component';
import { InventoryComponent } from './inventory/inventory.component';
import { MySalesComponent } from './my-sales/my-sales.component';
import { ReportsComponent } from './reports/reports.component';
import { ProfileComponent } from './profile/profile.component';
import { BlocksComponent } from './blocks/blocks.component';
import { BlockComponent } from './block/block.component';
import { ActivityComponent } from './activity/activity.component';
import { PartiesComponent } from './parties/parties.component';
import { QuarriesComponent } from './quarries/quarries.component';
import { AddInvoiceComponent } from './add-invoice/add-invoice.component';
import { AddItemComponent } from './add-item/add-item.component';
import { SlabsComponent } from './slabs/slabs.component';
import { YardsComponent } from './yards/yards.component';
import { CompanyComponent } from './company/company.component';
import { EditSlabsComponent } from './edit-slabs/edit-slabs.component';
import { EditBlockComponent } from './edit-block/edit-block.component';
import { UnitsComponent } from './units/units.component';
import { BatchTaskComponent } from './batch-task/batch-task.component';
import { EditThappiComponent } from './edit-thappi/edit-thappi.component';
import { MyQuotationComponent } from './my-quotation/my-quotation.component';


const routes: Routes = [
  {path: '', component:SigninComponent},
  {path: 'signup', component:SignupComponent},
  {path: 'changepassword', component:ChangepasswordComponent},
  {path: 'main', component:MainComponent, children: [
    {
      path:'add-invoice',
      component:AddInvoiceComponent
    },
    {
      path:'parties',
      component:PartiesComponent
    },
    {
      path:'quarries',
      component:QuarriesComponent
    },

    {
      path: 'activity',
      component: ActivityComponent,
    },
    {
      path: 'dashboard',
      component: DashboardComponent,
    },
    {
      path: 'manage-roles',
      component: ManageRolesComponent,
    },
    {
      path: 'reports',
      component: ReportsComponent,
    },
    {
      path: 'new-block',
      component: NewBlockComponent,
    },
    {
      path: 'yards',
      component: YardsComponent,
    },
    {
      path: 'inventory',
      component: InventoryComponent,
    },
    {
      path: 'my-sales',
      component: MySalesComponent,
    },
    {
      path: 'my-quotation',
      component: MyQuotationComponent,
    },
    {
      path: 'profile',
      component:ProfileComponent
    },
    {
      path: 'blocks',
      component: BlocksComponent,
    },
    {
      path: 'block',
      component: BlockComponent,
    },
    {
      path: 'add-item',
      component: AddItemComponent
    },
    {
      path: 'slabs',
      component: SlabsComponent
    },
    {
      path: 'company',
      component: CompanyComponent
    },
    {
      path: 'edit-block',
      component:EditBlockComponent
    },
    {
      path: 'edit-slabs',
      component:EditSlabsComponent
    },
    {
      path:'units',
      component:UnitsComponent
    },
    {
      path:'batch-task',
      component:BatchTaskComponent
    },
    {
      path:'edit-thappi',
      component:EditThappiComponent
    },

  ]},
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
